.team-card,
.project-card{
  &__img{
    display: block;
    position: relative;
    overflow: hidden;
    border-radius: 10px;
    &:hover{
      img{
        transform: scale(1.1);
      }
    }
    img{
      transition: all .3s ease-in-out;
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
    &-wrap{
      transform: translateZ(0);
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      overflow: hidden;
      border-radius: 10px;
    }
  }
}

.project-card{
  &--small{
    .project-card{
      &__img{
        padding-bottom: 129%;
      }

    }
  }
  &__img{
    padding-bottom: 80%;
  }
  &__title{
    display: block;
    margin-top: 27px;
  }
  &__desc{
    margin-top: 7px;
    color: $dark;
  }
}

.news-card{
  padding: 40px 40px 36px;
  background-color: #F8F8F8;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  &__date{
    padding-top: 32px;
    margin-top: auto;
  }
}

.gallery-card{
  display: block;
  position: relative;
  padding-bottom: 70.61%;
  overflow: hidden;
  border-radius: 10px;
  width: 100%;
  &:hover{
    img{
      transform: scale(1.1);
    }
  }
  img{
    transition: all .3s ease-in-out;
    position: absolute;
    top: 0;
    left: 0;
    object-fit: cover;
    width: 100%;
    height: 100%;
    border-radius: 10px;
  }
}

.team-card{
  &:hover{
    color: inherit;
  }
  &__img{
    padding-bottom: 135%;
  }
  &__title{
    margin-top: 23px;
  }
  &__desc{
    margin-top: 12px;
  }
}

.bio-card {
  &.slideToggle-block--active {
    .bio-card {
      &__btn {
        svg {
          transform: rotate(180deg);
        }
      }
    }
  }
  &:not(:last-child) {
    margin-bottom: 30px;
  }
  &__show {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 40px;
    min-height: 80px;
    background-color: #fff;
    cursor: pointer;
    border-radius: 10px;
  }
  &__title{
    font-size: 28px;
    font-weight: 500;
  }
  &__btn {
    height: 30px;
    svg {
      transition: all .225s;
    }
  }
  &__wrap {
    padding: 27px 39px 20px;
    max-width: 710px;
    color: $dark;
  }
  .mlist{
    margin-top: 18px;
  }
}


@media screen and (max-width: 992px){
  .project-card{
    &__img{
      padding-bottom: 129%;
    }
    &__title{
      margin-top: 18px;
    }
  }

  .news-card{
    padding: 27px 20px;
    &__title{
      br{
        display: none;
      }
    }
    &__date{
      padding-top: 24px;
    }
  }

  .gallery-card{
    padding-bottom: 70.6%;
  }

  .bio-card{
    &:not(:last-child){
      margin-bottom: 20px;
    }
    &__show{
      padding: 12px 20px;
      min-height: 54px;
    }
    &__btn{
      svg{
        width: 18px;
        height: auto;
      }
    }
    &__wrap{
      padding: 20px 0 10px;
    }
  }
}

