.team{
  padding-bottom: 75px;
  overflow: hidden;
  &__grid{
    margin-top: 42px;
    display: grid;
    grid-template-columns: repeat(3, calc(33.33% - (86px / 3)));
    gap: 60px 43px;
  }
}


.team-item {
  padding: 0 0 90px;
  &__inner {
    margin-top: 36px;
    display: grid;
    grid-template-columns: 36% calc(64% - 80px);
    gap: 80px;
  }
  &__left {
    img {
      display: block;
      width: 100%;
      border-radius: 10px;
    }
  }
  &__subtitle {
    margin-top: 12px;
    font-size: 20px;
    line-height: 150%;
    font-weight: 600;
  }
  &__text {
    margin-top: 40px;
    max-width: 694px;
  }
  &__link {
    margin-top: 90px;
  }
}
