@import "../vars.scss";
.breadcrumbs {
    &__item {
        font-size: 16px;
        line-height: 28px;
        position: relative;
        display: inline;
        transition: all .225s;
        color: #333;
        &:not(:first-child) {
            &:before {
                content: '|';
                margin: 0 7px 0 4px;
            }
        }
        &:last-child {
            color: #777;
        }
    }
    &__title{
        margin-top: 10px;
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        &__item{
            font-size: 12px;
            line-height: 18px;
            &:not(:first-child) {
                &:before {
                    margin: 0 5px 0 3px;
                }
            }
        }
    }
}

