.main-slider--project{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.about--project{
  padding-bottom: 85px;
  .about{
    &__title{
      max-width: 100%;
    }
  }
}

.about{
  &__items{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 40px;
    margin-top: 43px;
  }
  &__item{
    max-width: 360px;
    &-title{
      display: flex;
      line-height: 26px;
    }
    &-icon{
      height: 26px;
      width: 24px;
      display: flex;
      align-items: center;
      flex-shrink: 0;
      margin-right: 12px;
    }
    &-desc{
      color: $dark;
      margin-top: 16px;
    }
  }
  &__btn{
    margin-top: 56px;
    width: 320px;
    height: 65px;
  }
  &__video{
    margin-top: 90px;
    &-title{
      margin-top: 36px;
    }
    &-desc{
      font-size: 18px;
      line-height: 28px;
      color: $dark;
      margin-top: 11px;
      max-width: 846px;
    }
    &-download{
      display: flex;
    }
    &-link{
      margin-top: 16px;
      display: flex;
      align-items: center;
      font-size: 16px;
      color: #333333;
      transition: all .3s;
      font-weight: 500;
    }
    &-icon{
      flex-shrink: 0;
      margin-left: 32px;
    }
  }
}

.collection-gallery{
  padding: 58px 0 92px;
  .swiper{
    &-slide{
      width: 550px;
    }
  }
  .collection__slider:before{
    height: calc(100% + 92px);
  }
}

@media screen and (max-width: 992px){
  .about--project{
    padding-bottom: 42px;
    .about{
      &__title{
        font-size: 20px;
        line-height: 32px;
      }
    }
  }

  .about{
    &__items{
      grid-template-columns: 100%;
      gap: 30px;
    }
    &__item{
      max-width: 290px;
    }
    &__btn{
      margin-top: 45px;
      width: 100%;
    }
    &__video{
      margin-top: 50px;
      .video{
        padding-bottom: 60%;
        border-radius: 10px;
        overflow: hidden;
        img{
          position: absolute;
          left: 0;
          top: 0;
          height: 100%;
          object-fit: cover;
        }
      }
      &-title{
        margin-top: 26px;
      }
      &-desc{
        font-size: 14px;
        line-height: 22px;
        margin-top: 11px;
      }
      &-link{
        font-size: 14px;
        margin-top: 30px;
      }
      &-icon{
        margin-left: 29px;
        width: 20px;
        height: 10px;
        overflow: hidden;
        position: relative;
        svg{
          position: absolute;
          top: 0;
          right: 0;
          height: 100%;
          width: auto;
        }
      }
    }
  }

  .collection-gallery{
    padding: 25px 0 35px;
    .collection{
      &__slider{
        margin-top: 14px;
      }
    }
  }
}

@media screen and (max-width: 450px){
  .collection-gallery{
    .swiper{
      &-slide{
        max-width: 100%;
      }
    }
  }
}
