.footer{
  padding-bottom: 45px;
  &__info{
    font-size: 14px;
    line-height: 22px;
    margin: 26px 0 10px;
    max-width: 270px;
    color: $dark;
  }
  &__links{
    display: grid;
    grid-template-columns: 1fr;
    gap: 20px;
  }
  &__link{
    font-size: 14px;
    font-weight: 500;
  }
  &__title{
    margin-bottom: 2px;
  }
  &__contacts{
    &-grid{
      display: grid;
      grid-template-columns: 100%;
      gap: 25px;
    }
    &-item{
      font-size: 14px;
      line-height: 22px;
      color: $dark;
    }
  }
  &__right{
    width: 262px;
    flex-shrink: 0;
  }
  &__btn{
    margin-top: 31px;
    height: 65px;
    width: 100%;
  }
  &__socials{
    display: flex;
    align-items: center;
    margin-top: 57px;
    &-items{
      display: flex;
      align-items: center;
      margin-left: 8px;
    }
    &-item{
      background-color: #F2F2F2;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      margin-left: 10px;
      width: 36px;
      height: 36px;
      transition: .3s;
      &:hover{
        background-color: $primary;
        path{
          fill:#fff;
        }
      }
    }
  }
  &__c,
  &__by{
    font-size: 14px;
    line-height: 19px;
    color: $dark;
  }
}

.footer-top{
  &__inner{
    display: flex;
    justify-content: space-between;
    padding-top: 70px;
    border-top: 1px solid rgba(#707070,.18);
  }
}

.footer--no-border{
  .footer-top__inner{
    border: none;
  }
}

.footer-bottom{
  margin-top: 80px;
  &__inner{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding-bottom: 32px;
    &__links{
      order: 1;
    }
    &__contacts{
      order: 2;
    }
    &__left{
      order: 3;
      grid-column: 1/3;
    }
    &__right{
      order: 4;
      grid-column: 1/3;
      max-width: 350px;
      width: 100%;
    }
    &__link{
      font-size: 14px;
    }
    &__title{
      margin-bottom: 5px;
      &--cooperate{
        span{
          display: none;
        }
      }
    }
    &__info{
      margin-top: 15px;
    }
    &__socials{
      margin-top: 30px;
    }
    &__c,
    &__by{
      font-size: 12px;
      line-height: 16px;
    }
  }

  .footer-top{
    &__inner{
      padding-top: 45px;
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 45px 10px;
    }
  }

  .footer-bottom{
    margin-top: 35px;
    &__inner{
      display: grid;
      grid-template-columns: 100%;
      gap: 5px;
    }
  }
}
