.about-page{
    padding-bottom: 100px;
    &__desc{
        margin-top: 45px;
        max-width: 1090px;
    }
    &__video{
        margin-top: 80px;
        &-title{
            margin-bottom: 30px;
        }
    }
}

.about-ceo {
    margin-top: 60px;
    background: #F4F4F4;
    display: grid;
    grid-template-columns: 43.4% auto;
    border-radius: 10px;
    overflow: hidden;
    &__img {
        height: 100%;
        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    &__content {
        padding: 75px 72px 75px 102px;
    }
    &__desc {
        font-size: 18px;
        line-height: 30px;
        font-style: italic;
        color: $dark;
        position: relative;
        &:after,
        &::before {
            position: absolute;
            font-size: 50px;
            color: $primary;
            font-weight: 500;
            font-style: normal;
        }
        &:before {
            content: '';
            right: 100%;
            top: 0;
            margin-right: 15px;
            transform: translateY(-7px);
        }
        &:after {
            content: '';
            margin-left: 10px;
            transform: translateY(-7px);
        }
    }
    &__name {
        margin-top: 45px;
        font-size: 24px;
        line-height: 32px;
        font-weight: 600;
    }
    &__prof {
        margin-top: 9px;
        color: $dark;
    }
}

.direction{
    margin-top: 80px;
    &__items{
        display: grid;
        grid-template-columns: repeat(3,calc(33.33% - (30px * 2 / 3)));
        gap: 37px;
        margin-top: 30px;
    }
    &__item{
        border-radius: 6px;
        padding: 29px 29px 39px;
        background-color: #F8F8F8;
        min-height: 263px;
    }
    &__icon{
        width: 94px;
        height: 94px;
        img{
            max-width: 100%;
            max-height: 100%;
            display: block;
        }
    }
    &__subtitle{
        margin-top: 40px;
        font-size: 22px;
        line-height: 30px;
    }
}

.bio {
    padding: 90px 0 100px;
    background-color: #F8F8F8;
    &__desc{
        margin-top: 30px;
        color: #333333;
        font-size: 20px;
        line-height: 150%;
        font-weight: 600;
        max-width: 717px;
    }
    &__items{
        margin-top: 50px;
    }
}


