.projects{
  padding-bottom: 75px;
  overflow: hidden;
  &__grid{
    margin-top: 42px;
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 20px));
    gap: 60px 40px;
  }
  .project-card{
    opacity: 0;
    transition: all 1.6s;
    transform: translateY(90px);
  }
  .animated--active{
    opacity: 1 !important;
    transform: translate(0,0);
  }
}


@media screen and (max-width: 992px){
  .projects{
    padding-bottom: 50px;
    &__grid{
      margin-top: 24px;
      grid-template-columns: repeat(2, calc(50% - 10px));
      gap: 45px 20px;
    }
  }
}

@media screen and (max-width: 576px){
  .projects{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
