@import "../vars";
.mbtn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.mbtn-accent {
    background-color: $primary;
    color: #fff;
    &:hover {
        color: #fff;
        background-color: darken($primary, 5%);
    }
}

.mbtn-accent-outline{
    border: 2px solid $primary;
    color: $black;
    font-size: 16px;
    font-weight: 500;
    border-radius: 10px;
    &:hover{
        color: #fff;
        background-color: $primary;
    }
}

.swiper-button-prev,
.swiper-button-next{
    width: unset;
    height: unset;
}

.arrow-link,
.arrow-link2{
    display: flex;
    align-items: center;
    font-size: 16px;
    font-weight: 500;
    transition: all .3s;
    color: #333;
}

.arrow-link{
    svg{
        margin-left: 17px;
    }
}

.arrow-link2{
    svg{
        margin-right: 17px;
    }
}


@media screen and (max-width: 992px){
    .arrow-link{
        font-size: 14px;
        svg{
            width: 20px;
            height: auto;
            margin-left: 16px;
        }
    }
}
