.main{
  margin-top: -160px;
  overflow: hidden;
  position: relative;
  color: #fff;
  &::before{
    pointer-events: none;
    content: '';
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    width: 100%;
    height: 149px;
    background: linear-gradient(to bottom, #00151F, rgba(#000, 0%));
  }
  &__inner{
    height: 100%;
    display: flex;
    align-items: center;
    position: relative;
    z-index: 2;
  }
  &__type1{
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    position: relative;
  }
  &__title{
    font-size: 42px;
    line-height: 55px;
    font-family: $font2;
    letter-spacing: 4.2px;
    text-transform: uppercase;
    position: relative;
    z-index: 2;
  }
  &__year{
    position: relative;
    z-index: 2;
    margin-right: 65px;
    font-family: $font3;
    font-size: 180px;
    letter-spacing: 9px;
    display: flex;
    align-items: flex-end;
    span{
      text-transform: uppercase;
      font-size: 42px;
      line-height: 52px;
      letter-spacing: 4.2px;
      transform: translate(-10px,-20px);
      font-style: italic;
      font-weight: 700;
    }
  }
  &__rect{
    position: absolute;
    right: 285px;
    top: 50%;
    transform: translateY(-50%);
  }
  &__type2{
    padding-left: 145px;
    &-wrap{
      position: relative;
    }
    .main__rect{
      right: unset;
      left: -121px;
    }
  }
  &__link{
    display: flex;
    align-items: center;
    margin-top: 75px;
    font-size: 18px;
    font-family: $font2;
    transition: all .3s;
    svg{
      margin-left: 20px;
    }
    &:hover{
      color: #fff;
      svg{
        transform: translateX(5px);
      }
    }
  }
}

.main-slider{
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      padding: 174px 0;
      height: auto;
      background-size: cover;
      min-height: 800px;
      position: relative;
      display: flex;
      align-items: center;
      &:before{
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(#01151F,.35);
        pointer-events: none;
      }
    }
  }
  .container{
    height: 100%;
  }
}

.main-info{
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  display: flex;
  align-items: flex-end;
  //background-color: rgba(#000,.4);
  background: linear-gradient(to top, #00151F, rgba(#000, 0%));
  z-index: 2;
  color: #fff;
  padding-top: 15px;
  transform: translateY(200%);
  opacity: 0;
  animation: mainItemsShow 1s normal forwards ease-in-out;
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      min-height: 160px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      font-family: $font2;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        right: 100%;
        background-color: rgba(#D1D1D1,.6);
        height: 100%;
        width: 1px;
        transform: translateX(-2px);
      }
      &-next+.swiper-slide+.swiper-slide+.swiper-slide{
        &:before{
          opacity: 0;
        }
      }
    }
  }
  &__title{
    font-size: 38px;
    line-height: 47px;
    font-weight: 500;
    span:not(.main-info__title-num){
      font-size: 24px;
      line-height: 29px;
    }
    strong{
      font-weight: inherit;
    }
  }
  &__desc{
    margin-top: 4px;
    font-size: 13px;
    line-height: 24px;
    letter-spacing: 0.65px;
    text-transform: uppercase;
  }
}

.banner1{
  .main{
    &__title,
    &__year,
    &__rect{
      transform: translateX(1000px);
      opacity: 0;
      transition: all 1s;
    }
    &__year{
      transition-delay: 1s;
    }
    &__rect{
      transition-delay: 1.8s;
      transform: translateX(1000px);
    }
    &__title{
      transition-delay: 2.9s;
    }
  }
}

.banner1.swiper-slide-active{
  .main{
    &__title,
    &__year,
    &__rect{
      transform: translateX(0px);
      opacity: 1;

    }
    &__rect{
      transform: translate(0px,-50%);
    }
  }
}

.banner2{
  .main{
    &__title,
    &__rect{
      transform: translateX(-1000px);
      opacity: 0;
      transition: all 1s;
    }
    &__link{
      opacity: 0;
      transition: all 1s;
      transition-delay: 3s;
    }
    &__title{
      transition-delay: 1.8s;
    }
    &__rect{
      transition-delay: .6s;
    }
  }
}

.main--project._animate,
.banner2.swiper-slide-active{
  .main{
    &__title,
    &__rect{
      transform: translateX(0px);
      opacity: 1;
    }
    &__rect{
      transform: translate(0px,-50%);
    }
    &__link{
      opacity: 1;
    }
  }
}

@keyframes mainItemsShow {
  0% {
    transform: translateY(200%);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

.about{
  padding: 110px 0 115px;
  &__inner{
    position: relative;
  }
  &__title{
    max-width: 885px;
  }
  &__desc{
    margin-top: 22px;
    font-size: 18px;
    line-height: 28px;
    max-width: 725px;
    color: $dark;
  }
  &__text{
    position: absolute;
    right: 100%;
    bottom: 0;
    margin-right: 48px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    width: 18px;
    transform: translateY(0) !important;
    &:before{
      content: '';
      position: absolute;
      top: 23px;
      right: 2px;
      height: calc(100% - 46px);
      width: 2px;
      background-color: #E8E8E8;
    }
    &-title{
      font-size: 18px;
      text-transform: uppercase;
      color: #BBBBBB;
      font-weight: 600;
      font-family: $font2;
      white-space: nowrap;
      transform: rotate(-180deg) translate(-5px, 5px);
      background-color: #fff;
      position: absolute;
      bottom: 0;
      left: 0;
      padding-bottom: 20px;
      writing-mode:tb-rl;
    }
  }
  .animated{
    transition: all 1.6s;
    opacity: 0;
    transform: translateY(90px);
    &--active{
      opacity: 1;
      transform: translateY(0);
    }
  }
}

.collection{
  padding: 100px 0 80px;
  background-color: #F5F5F5;
  overflow: hidden;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__nav{
    display: flex;
    align-items: center;
  }
  .swiper{
    &-button{
      &-prev,
      &-next{
        position: static;
        margin-top: 0;
        transform: unset;
        &:hover{
          path{
            stroke: $primary;
          }
        }
      }
      &-next{
        margin-left: 26px;
      }
    }
    &-slide{
      &-next{
        .gallery-card{
          transition-delay: .3s;
        }
      }
      &-next+.swiper-slide{
        .gallery-card{
          transition-delay: .6s;
        }
      }
      &-next+.swiper-slide+.swiper-slide{
        .gallery-card{
          transition-delay: .9s;
        }
      }
    }
  }
  .project-card,
  .gallery-card{
    opacity: 0;
    transition: all 1.6s;
    transform: translateY(90px);
  }
  .animated--active{
    .project-card,
    .gallery-card{
      opacity: 1;
      transform: translate(0,0) !important;
    }
  }
  &__slider{
    margin-top: 30px;
    position: relative;
    &:before{
      //content: '';
      position: absolute;
      background-color: #F5F5F5;
      top: 0;
      right: 100%;
      height: 100%;
      width: 3000px;
      z-index: 2;
      transform: translateX(-20px);
      pointer-events: none;
    }
  }
  @media screen and (min-width: 992px) {
    .project-card {
      &__title {
        font-size: 20px;
        line-height: 30px;
        margin-top: 25px;
      }
    }
  }
}

.presentation{
  padding: 120px 0 48px;
  overflow: hidden;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 420px) 345px;
    gap: 75px;
  }
  &__desc{
    margin-top: 23px;
    color: $dark;
  }
  &__nav{
    display: flex;
    margin-top: 25px;
  }
  .video{
    transform: translateY(200px);
  }
  .animated{
    transform: translateY(200px);
    opacity: 0;
    transition: all 1.6s;
  }
  .animated--active{
    opacity: 1;
    transform: translateY(0) !important;
  }
}

.video{
  display: block;
  position: relative;
  overflow: hidden;
  opacity: 0;
  transition: all 1.5s;
  border-radius: 10px;
  &.animated--active{
    opacity: 1;
    transform: translateX(0);
  }
  &:hover{
    img{
      transform: scale(1.1);
    }
  }
  img{
    border-radius: 10px;
    width: 100%;
    display: block;
    transition: all .3s ease-in-out;
  }
  &__play{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    border-radius: 50%;
    width: 100px;
    height: 100px;
    svg{
      transform: translateX(5px);
    }
  }
}

.news{
  padding: 48px 0 90px;
  overflow: hidden;
  &__grid{
    margin-top: 30px;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 30px));
    gap: 60px;
  }
  .news-card{
    transition: all 1.6s;
    transform: translateY(130px);
    &:nth-child(even){
      transition-delay: .1s;
    }
  }
  .animated--active{
    transform: translateY(0);
  }
}

@media screen and (max-width: 1450px){
  .about{
    &__text{
      display: none;
    }
  }
}


@media screen and (max-width: 1100px){
  .news{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 15px));
      gap: 30px;
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    margin-top: -90px;
    &::before{
      height: 90px;
    }
    &__inner{
      align-items: unset;
    }
    &__type1{
      display: block;
      .main__title{
        padding-left: 80px;
      }
    }
    &__year{
      font-size: 96px;
      letter-spacing: 4.8px;
      margin-bottom: 16px;
      span{
        font-size: 22px;
        line-height: 27px;
        transform: translate(0,-15px);
        letter-spacing: 2.2px;
      }
    }
    &__title{
      font-size: 24px;
      line-height: 35px;
      letter-spacing: 2.4px;
    }
    &__rect{
      width: 173px;
      right: unset;
      left: 0;
      top: 102px;
      transform: unset;
    }
    &__type2{
      padding-left: 20px;
      position: relative;
      .main__rect{
        top: -30px;
        left: -40px;
      }
    }
    &__link{
      margin-top: 40px;
      font-size: 16px;
      svg{
        width: 27px;
        height: auto;
        margin-left: 15px;
      }
    }
  }

  .main-slider{
    .swiper{
      &-slide{
        padding: 120px 0;
        min-height: 520px;
      }
    }
  }

  .main-info{
    .swiper{
      &-slide{
        min-height: 100px;
        justify-content: flex-end;
        padding: 10px 5px 15px;

      }
    }
    &__title{
      font-size: 26px;
      line-height: 32px;
      span:not(.main-info__title-num){
        font-size: 18px;
        line-height: 22px;
      }
    }
    &__desc{
      font-size: 10px;
      letter-spacing: 0.5px;
      margin: 0;
    }
  }

  .banner1{
    .main{
      &__title,
      &__year,
      &__rect{
        transform: translateX(-1000px);
      }
    }
    &._animate{

    }
  }

  .banner1.swiper-slide-active{
    .main{
      &__rect{
        transform: translate(0px,0%);
      }
    }
  }

  .main--project._animate,
  .banner2.swiper-slide-active{
    .main{
      &__rect{
        transform: translate(0px,0%);
      }
    }
  }

  .about{
    padding: 42px 0;
    &__title{
      max-width: 600px;
    }
    &__desc{
      max-width: 500px;
      font-size: 14px;
      line-height: 22px;
      margin-top: 10px;
      br{
        display: none;
      }
    }
  }

  .collection{
    padding: 35px 0;
    &__nav{
      display: none;
    }
    &__slider{
      margin-top: 20px;
    }
    .project-card{
      &__title{

      }
    }
  }

  .presentation{
    padding: 45px 0 22px;
    &__inner{
      grid-template-columns: calc(100% - 325px) 300px;
      gap: 25px;
    }
  }

  .video{
    &__play{
      width: 52px;
      height: 52px;
      svg{
        width: 19px;
        height: auto;
        transform: translateX(3px);
      }
    }
  }

  .news{
    padding: 22px 0 50px;
    &__grid{
      grid-template-columns: 100%;
      margin-top: 20px;
    }
  }
}


@media screen and (max-width: 820px){
  .main-info{
    .swiper{
      &-slide{
        &-next+.swiper-slide+.swiper-slide{
          &:before{
            opacity: 0;
          }
        }
      }
    }
  }

  .collection{
    .swiper-slide{
      width: 310px;
      max-width: calc(100% - 20px);
    }
  }

  .presentation{
    &__inner{
      position: relative;
      grid-template-columns: 100%;
      padding-top: 47px;
    }
    &__title{
      position: absolute;
      top: 0;
      left: 0;
    }
    &__desc{
      margin: 0;
    }
    &__nav{
      margin-top: 22px;
    }
  }
}


@media screen and (max-width: 620px){
  .main-info{
    .swiper{
      &-slide{
        &-next+.swiper-slide{
          &:before{
            opacity: 0;
          }
        }
      }
    }
  }
}
