@import "../vars.scss";
.eco {
    padding: 0 0 20px;
    &__title {
        color: #333333;
    }
    &__video{
        margin-top: 45px;
    }
    &__desc {
        margin-top: 55px;
        max-width: 962px;
        color: #333333;
    }
    &__items {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 40px;
        margin-top: 40px;
    }
    &__text {
        color: $dark;
    }
}

.co {
    margin-top: 52px;
    display: grid;
    grid-template-columns: repeat(4, 25%);
    &__item {
        height: 270px;
        border: 1px solid #DCDCDC;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 32px;
        @media screen and (min-width: 768px){
            &:not(:last-child) {
                border-right: none;
            }
        }
        img {
            max-width: 100%;
            max-height: 100%;
        }
    }
}

@media screen and (max-width: 1150px){
    .eco{
        &__items{
            gap: 35px;
        }
    }
}

@media screen and (max-width: 992px){
    .eco{
        padding: 0 0 25px;
        &__desc{
            margin-top: 22px;
        }
        &__items{
            grid-template-columns: 100%;
            margin-top: 35px;
        }
    }

    .co{
        margin-top: 32px;
        &__item{
            height: 200px;
            padding: 32px 18px;
        }
    }
}

@media screen and (max-width: 768px){
    .co{
        grid-template-columns: 50% 50%;
        &__item{
            &:nth-child(even){
                border-left: none;
            }
            &:first-child,
            &:nth-child(2){
                border-bottom: none;
            }
        }
    }
}

@media screen and (max-width: 576px){
    .co{
        &__item{
            height: 155px;
        }
    }
}
