@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
//@import 'components/_fonts.scss';
.pagination {
    margin-top: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    &__item {
        height: 38px;
        min-width: 34px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0 6px;
        font-size: 22px;
        font-weight: 500;
        border-radius: 6px;
        font-variant-numeric: tabular-nums;
        -webkit-font-feature-settings: "tnum";
        font-feature-settings: "tnum";
        &:hover,
        &--active {
            background-color: $primary;
            border-color: $primary;
            color: #fff;
        }
    }
    &__btn {
        &--prev {
            margin-right: 42px;
        }
        &--next {
            margin-left: 42px;
        }
        &:hover {
            path {
                stroke: $black;
            }
        }
        &._disabled{
            path{
                stroke: #D5D5D5;
            }
        }
    }
    &__item {
        margin: 0 3px;
        &--active {
            pointer-events: none;
        }
    }
    &__more {
        display: none;
    }
}

.fg {
    position: relative;
    margin: 0;
    label{
        color: #333;
        font-size: 16px;
        line-height: 22px;
        display: block;
        margin-bottom: 4px;
    }
    input,
    textarea{
        border: 1px solid #E2E2E2;
        border-radius: 2px;
        padding: 0 10px;
        height: 40px;
        width: 100%;
        font-size: 16px;
        line-height: 22px;
        background-color: transparent;
        color: #333;
        transition: all .3s;
        &:focus {
            border-color: $primary;
        }
        &::placeholder{
            color: $gray;
        }
    }
    textarea{
        resize: none;
        padding-top: 8px;
        padding-bottom: 8px;
        height: 68px;
    }
    &--bottom{
        input{
            height: 60px;
            padding: 0;
            border-top: none;
            border-right: none;
            border-left: none;
            font-size: 18px;
        }
    }
}

.file-input {
    width: 210px;
    height: 50px;
    position: relative;
    display: flex;
    align-items: center;
    padding: 0 11px;
    background-color: #F4F4F4;
    font-size: 14px;
    border-radius: 6px;
    color: #333;
    &:hover {
        background-color: darken(#f4f4f4, 5%);
    }
    svg {
        margin-right: 15px;
    }
    input {
        position: absolute;
        z-index: 2;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        cursor: pointer;
    }
}

.check {
    position: relative;
    color: #888888;
    input {
        position: absolute;
        opacity: 0;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        z-index: 2;
        cursor: pointer;
        &:checked+label {
            &::before {
                background-repeat: no-repeat;
                background-position: center;
                background-image: url(../img/check.svg);
            }
        }
    }
    label {
        padding-left: 30px;
        position: relative;
        min-height: 22px;
        font-size: 14px;
        line-height: 22px;
        cursor: pointer;
        display: block;
        &:before {
            content: '';
            position: absolute;
            z-index: 1;
            border: 1px solid #9A9898;
            width: 18px;
            height: 18px;
            left: 0;
            top: 3px;
            border-radius: 2px;
        }
    }
    a {
        text-decoration: underline;
        position: relative;
        z-index: 3;
        &:hover {
            color: $primary;
        }
    }
}

.slideToggle-block {
    &__hidden {
        height: 0;
        overflow: hidden;
        transition: all .425s;
    }
}

.search-field {
    position: relative;
    &__btn {
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
    }
    input {
        font-size: 14px;
        padding: 12px 0;
        border: none;
        border-bottom: 1px solid rgba($dark,.4);
        border-radius: 0;
        &::placeholder{
            color: #CCCCCC;
        }
    }
}

.animated-opacity{
    &.animated{
        opacity: 0;
        transform: translateY(80px);
        transition: all 1.6s;
        &--active{
            transform: translateY(0);
            opacity: 1;
        }
    }
}

@media screen and (max-width: 992px){
    .pagination{
        display: none;
        &__more {
            display: flex;
            height: 51px;
            width: 500px;
            max-width: 100%;
            margin: 45px auto 0;
            font-size: 16px;
            font-weight: 500;
        }
    }
}
