@import '../vars';
@import '../mixins';
.modal{
    display: none;
    position: fixed;
    background: rgba(#000, .4);
    z-index: 100;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
    overflow-y: auto;
    padding: 50px 20px;
    &--active{
        display: flex;
    }
    &__dialog{
        padding: 24px 30px 36px;
        width: 300px;
        max-width: 100%;
        background-color: #fff;
        position: relative;
        box-shadow: 0 3px 22px rgba(#000,.16);
    }
    &__close{
        position: absolute;
        top: 28px;
        right: 17px;
    }
    &__title{
        font-size: 18px;
        line-height: 20px;
        font-weight: 700;
        font-family: $font2;
    }
    &__form{
        margin-top: 15px;
        &-grid{
            display: grid;
            grid-template-columns: 100%;
            gap: 15px;
        }
    }
    &__hint{
        font-size: 12px;
        line-height: 18px;
        margin-top: 20px;
        color: #777;
        a{
            text-decoration: underline;
        }
    }
    &__btn{
        margin-top: 16px;
        width: 100%;
        height: 56px;
        border-radius: 6px;
    }
}


