.career{
    padding-bottom: 40px;
    &__img{
        margin-top: 47px;
        border-radius: 10px;
        overflow: hidden;
        position: relative;
        &::before{
            pointer-events: none;
            content: '';
            position: absolute;
            z-index: 2;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 194px;

        }
        img{
            width: 100%;
            display: block;
        }
    }
    &__subtitle{
        background: linear-gradient(to top, #00151F, rgba(#000, 0%));
        position: absolute;
        min-height: 194px;
        padding: 37px 140px 37px 37px;
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
        width: 100%;
        left: 0;
        bottom: 0;
        color: #fff;
    }
}

.advantages{
    margin-top: 65px;
    &__top{
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        gap: 28px;
    }
    &__title{
        grid-column: 1/4;
    }
    &__link{
        color: $primary;
        svg{
            margin-left: 28px;
        }
    }
    &__items{
        display: grid;
        grid-template-columns: repeat(4,calc(25% - (28px * 3 / 4)));
        gap: 28px;
        margin-top: 30px;
    }
    &__item{
        border-radius: 6px;
        padding: 16px 20px 24px;
        background-color: #F8F8F8;
        min-height: 200px;
    }
    &__icon{
        width: 60px;
        height: 60px;
        img{
            max-width: 100%;
            max-height: 100%;
            display: block;
        }
    }
    &__subtitle{
        margin-top: 55px;
    }
}

.resume {
    padding: 40px 0 80px;
    &__inner {
        display: grid;
        grid-template-columns: 51.64% calc(48.36% - 65px);
        gap: 65px;
    }
    &__left{
        img{
            width: 100%;
            display: block;
            border-radius: 10px;
        }
    }
    &__title {
        margin-top: 34px;
        max-width: 350px;
    }
    &__desc {
        margin-top: 25px;
        max-width: 590px;
    }
    &__form {
        margin-top: 18px;
        display: grid;
        grid-template-columns: 100%;
        gap: 13px;
        &-wrap {
            display: flex;
            align-items: flex-end;
        }
    }
    &__hint {
        margin-top: 35px;
        font-size: 14px;
        line-height: 22px;
        color: $dark;
        a {
            color: $primary;
            &:hover {
                text-decoration: underline;
            }
        }
    }
    &__check {
        max-width: 450px;
    }
    &__btn {
        padding: 0;
        width: 270px;
        height: 65px;
        margin-top: 34px;
        border-radius: 10px;
    }
    &__download {
        display: flex;
        align-items: center;
        font-size: 16px;
        line-height: 237%;
        color: $primary;
        margin-top: 18px;
        svg {
            margin-right: 10px;
        }
        &:hover {
            span {
                text-decoration: underline;
            }
        }
    }
    .file-input {
        margin-left: 31px;
        flex-shrink: 0;
    }
    .fg{
        flex-grow: 1;
    }
}

@media screen and (max-width: 1150px){
    .resume{
        &__inner{
            gap: 30px;
        }
    }
}

@media screen and (max-width: 992px){
    .resume{
        .file-input{
            margin-left: 11px;
        }
        &__title{
            margin-top: 20px;
            max-width: 220px;
        }
        &__form{
            margin-top: 8px;
        }
        &__hint{
            font-size: 12px;
            margin-top: 13px;
        }
        &__check{
            margin-top: -2px;
        }
        &__btn{
            width: 100%;
        }
    }
}

@media screen and (max-width: 768px){
    .resume{
        &__inner{
            grid-template-columns: 1fr;
            gap: 55px;
            padding: 0;
            border: none;
        }
        &__right{
            order: -1;
        }
    }
}

@media screen and (max-width: 576px){
    .resume{
        padding: 10px 0 16px;
    }
}
