.logo{
  display: block;
  flex-shrink: 0;
  width: 151px;
  img,
  svg{
    width: 100%;
    height: auto;
    display: block;
  }
}

.header{
  padding: 44px 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 3;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    position: relative;
  }
  &__logo{
    margin-right: auto;
  }
  &__lang{
    margin-left: 160px;
    &-current{
      letter-spacing: 0.9px;
      font-size: 15px;
      line-height: 20px;
      text-transform: uppercase;
      transition: all .3s;
      &:hover{
        color: $primary !important;
        cursor: pointer;
      }
      svg{
        margin-left: 9px;
      }
    }
  }
  &__burger {
    z-index: 15;
    display: flex;
    align-items: center;
    align-self: flex-start;
    width: 34px;
    height: 24px;
    position: absolute;
    margin-right: 54px;
    left: -88px;
    top: 0;
    span {
      height: 2px;
      width: 100%;
      display: block;
      background-color: #444;
      transition: all .225s;
      &:nth-child(1) {
        position: absolute;
        top: 0;
        width: 50%;
      }
      &:nth-child(3) {
        position: absolute;
        bottom: 0;
        right: 0;
        width: 50%;
      }
    }

  }

  &._light{
    .nav{
      color: #fff;
    }
    .header{
      &__lang{
        &-current{
          color: #fff;
          path{
            stroke:#fff;
          }
        }
      }
      &__logo{
        path:not(.not-change){
          fill: #fff;
        }
      }
      &__burger{
        span{
          background-color: #fff;
        }
      }
    }
  }
  &._open{
    .header{
      &__inner{
        z-index: 16;
      }
      &__logo{
        path:not(.not-change){
          fill: #333;
        }
      }
      &__lang-current{
        color: #444444;
      }
      &__burger{
        span {
          background-color: #444;
          &:nth-child(1) {
            width: 100%;
            transform: translateY(11px) rotate(-45deg);
          }
          &:nth-child(2) {
            transform: scale(0);
          }
          &:nth-child(3) {
            width: 100%;
            transform: translateY(-11px) rotate(45deg);
          }
        }
        &~.menu {
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
    .nav{
      opacity: 0;
    }
    .menu{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  &__item{
    position: relative;
    &:not(:last-child){
      margin-right: 8px;
    }
    &:hover{
      .nav__hidden{
        transform: translate(-50%,-2px);
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
  &__hidden{
    min-width: 100%;
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translate(-50%,30px);
    padding-top: 10px;
    transition: all .4s;
    opacity: 0;
    pointer-events: none;
    &-inner{
      border-radius: 10px;
      background-color: #343333;
      padding: 19px 0;
      box-shadow: 0 9px 20px rgba(#000,.4);
    }
  }
  &__link,
  &__link2{
    display: flex;
    align-items: center;
    height: 35px;
    transition: all .3s;
    font-weight: 500;
  }
  &__link{
    letter-spacing: 0.9px;
    font-size: 15px;
    line-height: 20px;
    text-transform: uppercase;
    padding: 0 20px;
    border-radius: 4px;
    &._active,
    &:hover{
      background-color: $primary;
      color: #fff;
    }
  }
  &__link2{
    color: #fff;
    font-size: 16px;
    padding: 0 25px;
    white-space: nowrap;
    &:hover{
      background-color: #464646;
      color: #fff;
    }
  }
}

.menu{
  transition: all .3s;
  pointer-events: none;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  overflow-y: auto;
  padding: 145px 0 50px;
  background-color: #fff;
  z-index: 14;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 400px) 360px;
    gap: 20px;
    max-width: 1060px;
  }
  &__left{
    width: 540px;
    display: flex;
    flex-direction: column;
  }
  &__title{
    margin-bottom: 30px;
    font-size: 26px;
    font-weight: 600;
    font-family: $font2;
  }
  &__links{
    display: grid;
    grid-template-columns: 1fr;
    gap: 26px;
    &-wrap{
      display: flex;
      justify-content: space-between;
      margin-bottom: 30px;
    }
  }
  &__link{
    font-size: 18px;
    //color: #484848;
    transition: all .3s;
    font-family: $font4;
    font-weight: 500;
    &:hover{
      color: #E56606;
    }
  }
  &__project{
    .project-card{
      &__title{
        font-size: 20px;
        line-height: 30px;
        margin-top: 25px;
      }
    }
  }
  &__search{
    margin-top: auto;
  }
  &__contacts{
    display: none;
  }
}


@media screen and (max-width: 1450px) {
  .header {
    &__burger {
      position: relative;
      left: unset;
      top: unset;
    }
  }
}


@media screen and (max-width: 1100px){
  .header{
    &__lang{
      margin-left: 60px;
    }
  }
}


@media screen and (max-width: 992px) {
  .nav{
    display: none;
  }

  .logo{
    width: 104px;
  }

  .header{
    padding: 0;
    &:not(._light){
      background-color: #fff;
      box-shadow: 0 3px 6px rgba(#000,.16);
    }
    &__inner{
      padding: 18px 0;
    }
    &__burger {
      margin-right: 23px;
      width: 24px;
      height: 20px;
      align-self: unset;
    }
    &._open{
      .header{
        &__inner{
          background-color: #fff;
        }
        &__burger{
          span {
            &:nth-child(1) {
              transform: translateY(9px) rotate(-45deg);
            }
            &:nth-child(3) {
              transform: translateY(-9px) rotate(45deg);
            }
          }
        }
      }
    }
  }

  .menu{
    padding: 0;
    &__inner{
      grid-template-columns: 100%;
      padding: 90px 47px;
    }
    &__left{
      width: unset;
    }
    &__right,
    &__title{
      display: none;
    }
    &__links{
      gap: 20px;
      &-wrap{
        display: grid;
        grid-template-columns: 100%;
        gap: 20px;
      }
    }
    &__contacts{
      display: block;
      margin-top: 35px;
    }
    &__phone{
      font-size: 18px;
      line-height: 24px;
      font-weight: 500;
    }
    &__address{
      font-size: 14px;
      line-height: 21px;
      margin-top: 3px;
      color: $dark;
    }
    &__mail{
      font-size: 12px;
      line-height: 21px;
      color: $primary;
      &:hover{
        color: $black;
      }
    }
  }
}
