.display1,
.display2,
.display3,
.display4,
.display5{
  font-family: $font2;
  font-weight: 600;
}

.display1{
  font-size: 56px;
  line-height: 70px;
}

.display2 {
  font-size: 40px;
  line-height: 50px;
}

.display3{
  font-size: 32px;
  line-height: 39px;
}

.display4{
  font-size: 24px;
  line-height: 30px;
}
.display5{
  font-size: 20px;
  line-height: 32px;
}

.title{
  font-size: 28px;
  line-height: 36px;
  font-weight: 600;
  font-family: $font2;
}

.bold{
  font-weight: 700;
}

.semi-bold{
  font-weight: 600;
}

.medium{
  font-weight: 500;
}


.accent-text{
  color: $primary;
}

.body-text{
  font-size: 16px;
  line-height: 24px;
  p{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 10px;
    }
  }
}

.mlist {
  li {
    padding-left: 30px;
    position: relative;
    font-size: 16px;
    line-height: 24px;
    &:not(:last-child) {
      margin-bottom: 15px;
    }
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      width: 18px;
      height: 4px;
      border-radius: 10px;
      background-color: $primary;
      left: 0;
      top: 10px;
    }
    a {
      color: $primary;
      &:hover {
        text-decoration: underline;
      }
    }
  }
}

@media screen and (max-width: 992px){
  .display1{
    font-size: 26px;
    line-height: 34px;
  }

  .display2,
  .display3{
    font-size: 22px;
    line-height: 26px;
  }

  .display4{
    font-size: 18px;
    line-height: 26px;
  }

  .display5{
    font-size: 16px;
    line-height: 24px;
  }

  .body-text{
    font-size: 14px;
    line-height: 20px;
  }
}
